.App {
  /* display: flex; */
  flex-direction: column; /* Stack children vertically */
  text-align: center;
  padding: 6px;
  margin: auto;
}

.FlexContainer {
  display: flex;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.my-actions { margin: 10px}
.order-1 { order: 1; }
.order-2 { order: 2; }
.order-3 { order: 3; }

.right-gap {
  margin-right: auto;
}
.Axis-Select {
  display: inline-block;
  width: 50%;
}

.Title {
  font-size: 2em;
  text-align: center;
  margin: 0;
}

.LeaderboardContainer {
  /* flex: 1; */
  width: 40%;
  height: 100%;
  display: inline-block;
  vertical-align: top;
}

.GraphContainer {
  /* flex: 2; */
  width: 60%;
  display: inline-block;
}

